export default {
  created() {
    this.$nuxt.$on('login', this.openLogin)
  },
  destroyed() {
    this.$nuxt.$off('login', this.openLogin)
  },
  data() {
    return {
      loginOpen: false,
    }
  },
  methods: {
    openLogin() {
      this.loginOpen = true
    },
  },
}
